import { Component, OnInit } from '@angular/core';
import { Title }     from '@angular/platform-browser';
declare var $: any;

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  product = [
    {
      cN: '工廠作業流程及品管自動化管理',
      cD: ['結合人工智慧系統與雲端科技，改善傳統作業流程弊端與疏漏', '提升產線效能及效率，並優化品管質量，以達成生產效能最佳化的結果'],
      eN: 'Automatic Production Line Management and Quality Control',
      eD: ['Our system combines Machine Learning and Cloud Computing tech to improve traditional operating system with better efficiency, higher yield rate and more effective management, optimizing customers’ product line capacity .']
    },
    {
      cN: 'AOI(自動光學檢測)',
      cD: ['高量測精度物體辨識，量測項目包含：探測產線金屬物體形變及刮痕等瑕疵與檢測金屬鑄件表面刻印等、辨識產線塑膠材質均勻度及透光度等性質、樹脂成型品外觀檢測、檢測印刷缺陷等影像對比、量測零件尺寸及顏色等項目、檢測物件位置與包裝方向等、檢測零件定位、偏移誤差與調整各項機械視覺應用，設定簡易、操作方便，有效杜絕人為誤差。'],
      eN: 'AOI(Automated Optical Inspection)',
      eD: [
        'High accuracy object measurement includes：',
        '● Inspect metal product’s deformation, scratch and other flaws.',
        '● Detect marks on metal serface.',
        '● Identify uniformity, transmittance, appearance and other characteristics of plastic product.',
        '● Inspect printing flaws and image comparision.',
        '● Measure size, color and other characteristics of components.',
        '● Identify object’s location, position bias, packing direction and other optics appliances.',
        '● Easy for system setting and operating, preventing missoperation'
      ],
      href: 'p1'
    },
    {
      cN: '雲端軟件功能設定及執行',
      cD: ['於雲端平台設置所需功能項目，下載至工廠本地端執行操控，或於雲端服務中執行啟動工作，可及時與彈性控制工作功能設定，有效管理產線作業，加強企業管控功能，協助企業整併複雜環境。'],
      eN: 'Cloud – based Systems Administration', eD: ['Customize your service setting and execute online, or download the software to factory’s devices and execute.', 'Efficiently manage product line anywhere, anytime, merge complicated operations into easily-used system and improve operating management.']
    }
  ];
  detail = [
    {
      id: 'p1',
      dT: '關於AOI',
      dE: 'Automated Optical Inspection',
      dD: ['係為一種高精度的光學影像檢測系統，運用機器視覺做為檢測標準技術，作為改良傳統上以人力使用光學儀器進行檢測的缺點，應用層面包括從高科技產業之研發、製造品管等領域。',
        'AOI利用光學儀器取得成品的表面狀態，再以電腦影像處理技術來檢出異物或圖案異常等瑕疵，因為是非接觸式檢查，所以可在中間工程檢查半成品。',
        '高精度光學影像檢測系統，包含量測鏡頭技術、光學照明技術、定位量測技術、電子電路測試技術、影像處理技術及自動化技術應用等領域。'],
      cN: '自動光學檢測設備組成部位',
      component: [
        { cN: '光學', detail: ['CCD Camera(檢測鏡頭)', '光源(上下光源打光)', 'Review Camera(複檢確認鏡頭)'] },
        { cN: '系統主機', detail: ['CIM系統主機', '系統主機螢幕', '瑕疵檢查主機', '瑕疵檢查主機螢幕'] },
        { cN: '網路連結', detail: ['集線器(Hub)', 'Network──乙太網路、CC-Link'] },
      ]
    },

  ];
  constructor(private titleService: Title) { }

  ngOnInit() {
    this.titleService.setTitle("Bear-產品介紹Product");
  }
  moveTo(n) {
    $("html, body").animate({ "scrollTop": $("#" + n).offset().top }, 900);
  }
}



