import { Component, OnInit} from '@angular/core';
import { Title }     from '@angular/platform-browser';
declare var $: any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  n = 0;
  moving = false;
  array: String[] = ['first', 'up'];
  fontSize = '2em';

  constructor(private titleService: Title) { }
  ngOnInit() {
    this.titleService.setTitle("Bear");
  }
  ngAfterViewInit() { }

  mouseWheelUpFunc() {
    if (!this.moving) {
      if ($("#p0" + (this.n - 1)).length > 0) {
        this.n--;
        this.moveTo(this.n);
      }
    }
  }
  mouseWheelDownFunc() {
    if (!this.moving) {
      if ($("#p0" + (this.n + 1)).length > 0) {
        this.n++;
        this.moveTo(this.n);
      }
    }
  }
  moveTo(n) {
    if (!this.moving) {
      this.n = n;
      this.moving = true;
      let thisObject = this;
      $(".btn").removeClass("btn-danger");
      $("#" + this.n).addClass("btn-danger");
      $("html, body").animate({ "scrollTop": $("#p0" + this.n).offset().top }, 1200, function() { thisObject.moving = false; })
    }
  }
}



