import { Component, OnInit } from '@angular/core';
import { Title }     from '@angular/platform-browser';

@Component({
  selector: 'app-recruit',
  templateUrl: './recruit.component.html',
  styleUrls: ['./recruit.component.css']
})
export class RecruitComponent implements OnInit {
  jobOpening = [
    {
      title: '前端工程研發團隊',
      intern: '正職 / 實習',
      content: ['編輯與升級公司線上服務介面', '參加公司後續產品之使用者介面設計與實作'],
      acquire: ['與其他工程師合作將編寫網頁之技巧運用在公司線上服務', '運用網頁框架在其他商用服務', '獲得自己主持子計畫的機會'],
      required: ['Html \\ Javascript \\ CSS \\ Typescript \\ Angular2 \\ Git'],
      additional: ['SASS \\ SCSS \\ socket.io \\ mongodb \\ express'],
      welfare: ['薪資待遇：面議'],
      ps: '請於應徵資料註明應徵實習生或全職工程師'
    }, {
      title: '業務專員',
      intern: '正職',
      content: ['新客戶、市場開發', '維持客戶關係、客戶拜訪開發', '售後滿意度維持、公司最新消息通知客戶', '顧客意見回饋、整理客戶資料', '定期業績、績效自評與回報', '其他客戶服務與接待事宜'],
      acquire: [],
      required: ['良好溝通能力', '良好時間控管、自我管理技能', '工作態度積極、主動、細心', '對產品與客戶充滿熱忱', '對業務和BD工作有興趣'],
      additional: ['有業務相關經驗', '涉獵工業機械相關領域', '了解新創或相關產業生態', '清晰的邏輯思辯能力', '基礎英文溝通能力'],
      welfare: ['薪資待遇：面議'],
      ps: ''
    }, {
      title: '影像識別工程師',
      intern: '正職',
      content: ['與其他工程師合作，完成各項專案計畫'],
      acquire: [],
      required: ['熟悉 C／C++', '有電腦視覺或影像處理相關開發經驗（學校專題或畢業論文研究題目也算）', '熟悉Linux 系統'],
      additional: ['熟悉訊號處理', '熟悉曲面幾何學'],
      welfare: ['薪資待遇：45000~75000'],
      ps: ''
    }, {
      title: '嵌入式系統工程師',
      intern: '正職',
      content: ['與其他工程師合作，完成各項專案計畫'],
      acquire: [],
      required: ['熟悉 Linux 指令，會使用embedded linux 或是開發板', '熟悉 C／C++', '熟悉嵌入系統原理', '有撰寫驅動程式相關經驗'],
      additional: ['有 arm 架構開發板經驗'],
      welfare: ['薪資待遇：45000~75000'],
      ps: ''
    }
  ];
  photo = ['1.jpg', '2.jpg', '3.jpg'];
  mobileBlock = "none";
  webBlock = "none";
  constructor(private titleService: Title) { }

  ngOnInit() {
    this.titleService.setTitle("Bear-招募資訊Recruit");
  }
}



